.toFlexImages {
   display:flex;
   justify-content: center;
   flex-wrap: wrap;
}

.toCenterFigcaptionTextAlign, .toCenterFigcaptionFitContent {
   display: inline-block;
   text-align: center;
   margin: 2em 2em 0 2em;
   padding: 5px;
   background-color: $green25;
}

.toCenterFigcaptionFitContent figcaption {
   max-width: fit-content;
}

.sectionRepresent {
   cursor:pointer;
   background-color: $green75;
   color: $green0;
}

.sectionRepresent:hover {
   opacity: .85
}

figcaption  {
   font-size: .7em;
}