@import './commonStyles/colorsScheme.scss';
@import './commonStyles/fonts.scss';

html, body {
   height: 100%;
   margin:0;
   padding: 0;
 }

body {
   background-color: $green0;
   color: $green100;
}

a {
   text-decoration: none;
}

.toFlex {
   display: -webkit-box;    
   display: -moz-box;       
   display: -ms-flexbox;   
   display: -webkit-flex;    
   display: flex; 
}

h1, h2, h3, h4 {
   font-family: 'NatureCap';
}

#main { 
   order: 2;
   width:85%;
   padding:5px;
   border-top: 1px solid $green100; 
   border-bottom: 1px solid $green100; 
p, span, figcaption {
   font-family:'Nature';
}
